<template>
  <div class="invitation" @scroll="onScroll">
    <InvitationCard v-for="data in clonePages" :key="data.id" :detail="data"></InvitationCard>
  </div>
</template>

<script>
import InvitationCard from '@/views/InvitationCard/components/InvitationCard'
import pages from '@/views/InvitationCard/data/pages'
import { cloneDeep } from 'lodash'
import { ref } from 'vue'
export default {
  name: 'InvitationView',
  components: { InvitationCard },
  setup () {
    const clonePages = ref(cloneDeep(pages))
    let timerScrollEndDetect
    function onScroll (evt) {
      const target = evt.target
      clearTimeout(timerScrollEndDetect)
      timerScrollEndDetect = setTimeout(function () {
        // 100
        // 
        ;[].slice.call(target.children).forEach(function (eleList, index) {
          if (
            Math.abs(eleList.getBoundingClientRect().top - target.getBoundingClientRect().top) < 10
          ) {
            // 
            eleList.classList.add('active')
            console.log(clonePages.value[index])
            clonePages.value[index].isActive = true
            // 
            // result.innerHTML = '' + (index + 1) + ''
          } else {
            eleList.classList.remove('active')
            clonePages.value[index].isActive = false
          }
        })
      }, 100)
    }
    return {
      clonePages,
      onScroll
    }
  }
}
</script>

<style scoped lang="less">
.invitation {
  @apply h-full overflow-x-hidden overflow-y-auto;
  scroll-snap-type: y mandatory;
  //scroll-snap-stop: always;
  scroll-behavior: smooth;
}
</style>
